/**
 * @description: 基础表单
 * @author: liuyu
 */

import React from 'react';
import { Row, Col, Form, Icon } from 'antd';

import styles from './style.less';

const FormItem = Form.Item;
const BASE_FORMITEM_LAYOUT = {
    labelCol: { span: 8 },
    wrapperCol: { span: 15 },
};

/**
 * 基础表单
 * @prop {Array} items - 查询条件
 * @prop {Array} extraItems - 折叠查询条件
 * @prop {Array} rowItems - 是否独占一行
 * @prop {Array} buttons - 操作按钮
 * @prop {Object} formItemLayout - 布局参数
 * @prop {function} onSubmit - 表单提交时回调
 * @method getForm - 获取form对象
 */
@Form.create()
class BaseForm extends React.PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            fold: true,
        };
    }
    /**
     * 获取form对象
     */
    getForm = () => this.props.form;
    /**
     * 表单提交
     */
    handleFormSubmit = (e) => {
        e.preventDefault();

        const { form, onSubmit } = this.props;
        if (typeof onSubmit === 'function') {
            onSubmit(form.getFieldsValue(), form);
        }
    }
    /**
     * 展开/收起多余查询条件
     */
    handleFoldClick = () => {
        const { fold } = this.state;
        this.setState({ fold: !fold });
    }
    renderFormItems = (item) => {
        const { formItemLayout, form } = this.props;
        const { getFieldDecorator } = form;
        return (
            <FormItem
                {...{
                    ...BASE_FORMITEM_LAYOUT,
                    ...formItemLayout,
                    ...item.formItemLayout,
                }}
                label={item.label}
            >
                {getFieldDecorator(item.field, {
                    initialValue: '',
                    ...item.options
                })(
                    React.cloneElement(
                        item.content,
                        {
                            style: { width: '100%' },
                        },
                    )
                )}
            </FormItem>
        );
    }
    renderItems = (items) => {
        if (!(items && items.length > 0)) {
            return null;
        }

        return items.map((one, i) => {
            const { lg = 6, md = 8, sm = 24 } = one.rowLayout || {};
            return (
                <Col
                    key={i}
                    lg={lg}
                    md={md}
                    sm={sm}
                    style={{ marginBottom: 10, ...one.style }}
                >
                    {one.isFormItem === false ? one.content : (
                        this.renderFormItems(one)
                    )}
                </Col>
            );
        });
    }
    renderRowItems = (items) => {
        if (!(items && items.length > 0)) {
            return null;
        }

        return items.map((one, i) => {
            return (
                <Row key={i}>
                <Col
                    style={{ marginBottom: 10, ...one.style }}
                >
                    {one.isFormItem === false ? one.content : (
                        this.renderFormItems(one)
                    )}
                </Col>
                </Row>
            );
        });
    }
    render() {
        const { items, extraItems, buttons, rowItems } = this.props;
        return (
            <Form
                layout="horizontal"
                className={styles.form}
                onSubmit={this.handleFormSubmit}
            >
                <div style={{width: '100%'}}>
                    {rowItems && (
                        <div>
                            {this.renderRowItems(rowItems)}
                        </div>
                    )}
                    <Row>
                        {this.renderItems(items)}
                    </Row>
                    {extraItems && (
                        <React.Fragment>
                            <Row className={styles.extraContainer}>
                                {this.renderItems(extraItems)}
                            </Row>
                            <Row className={styles.foldContainer}>
                                <p onClick={this.handleFoldClick}>
                                    <Icon
                                        type="down"
                                        style={{ marginLeft: 4 }}
                                    />
                                </p>
                            </Row>
                        </React.Fragment>
                    )}
                </div>
                {buttons && (
                    <div className={styles.buttons}>
                        {buttons.map((one, index) => {
                            return React.cloneElement(
                                one,
                                { key: index },
                            );
                        })}
                    </div>
                )}
            </Form>
        );
    }
}

export default BaseForm
