import React, { Component } from 'react'
import ReactMarkdown from 'react-markdown'
import {
  Button, Modal, Drawer, List, Select, Tag
} from 'antd';
import BaseForm from '@/components/BaseForm'
import TagSelect from '@/components/TagSelect'

import {connect} from 'dva'

import BaseContent from '@/components/BaseContent'
import UtilGlobal from '../../utils/global'
import styles from './style.module.less'


const Option = Select.Option;

const {basePath} = UtilGlobal;

/**
 * 题目详情
 */
class QuestionDetailModul extends Component {
  handleOk = () => {
    this.props.handleOk(this.props.detail)
  }

  handleCancel = () => {
    this.props.handleCancel()
  }

  render () {
    const {title, questions_stem} = this.props.detail
    return (
      <Modal
          visible={this.props.visible}
          width={800}
          footer={[
            <Button size="large" key="save" type="primary" onClick={this.handleOk}>添加</Button>,
            <Button size="large" key="cancel" onClick={this.handleCancel}>取消</Button>,
          ]}
          bodyStyle={{
            padding: '24px 80px',
          }}
        >
          <div className={styles.addModal}>
          <div className={styles.questionitem}>
            <h4>{title}</h4>
            <ReactMarkdown className="react-markdown" source={questions_stem}></ReactMarkdown>
          </div>
          </div>
        </Modal>
    )
  }
}

/**
 * 所有题目列表
 */

class QuestionItems extends Component {
  constructor (props) {
    super(props)
    this.state = {
      visible: false,
      questions_detail: {}
    }
  }
  
  add = (detail) => {
    this.handleCancel()
    this.props.handleOk(detail)
  }

  detail = (detail) => {
    this.setState({
      questions_detail: detail,
      visible: true
    })
  }

  handleCancel = () => {
    this.setState({
      visible: false
    })
  }

  render () {
    return (
      <div>
        <List
          className="demo-loadmore-list"
          itemLayout="horizontal"
          dataSource={this.props.items}
          
          renderItem={item => (
            <List.Item actions={[
              <a href="javascript:;" onClick={() => this.add(item)}>添加</a>,
              <a href="javascript:;" onClick={() => this.detail(item)}>详情</a>
            ]}>
                <div>
                  <List.Item.Meta
                    title={item.title}
                  />
                  <div style={{marginTop: 10}}>
                    <div style={{marginBottom: 10}}>
                      <Tag color="blue">{item.questions_type_text}</Tag>
                      <Tag color="geekblue">{item.subject_text}</Tag>
                      <Tag color="orange">{item.exam_name}</Tag>
                    </div>
                    <span >{item.user_name} 发布</span>
                  </div>
                </div>
            </List.Item>
          )}
        />
        <QuestionDetailModul 
          visible={this.state.visible} 
          handleCancel={this.handleCancel} 
          handleOk={this.add}
          detail={this.state.questions_detail} 
        />
      </div>
    )
  }
}

let mapStateToProps = store => {
  return {
    ...store.watchQuestions,
    ...store.main,
    loading: store.loading.global
  }
}
@connect(mapStateToProps)
class QuestionDrawer extends Component {
  constructor (props) {
    super(props)
    this.state = {
      visible: false,
      subject_id: '',
      questions_type_id: '',
      exam_id: '',
      questions_detail: {}
    }
  }

  static getDerivedStateFromProps = (newProps,nowState)=>{
    return {
      ...nowState,
      ...newProps
    }
  }

  getQuestions (filter = {}) {
    this.props.dispatch({type:'watchQuestions/getQuestions', filter})
  }

  componentDidMount(){
    this.getQuestions()
    this.props.dispatch({type:'watchQuestions/getAllSubject'})
    this.props.dispatch({type:'watchQuestions/getQuestionsType'})
    this.props.dispatch({type:'watchQuestions/getAllExamType'})
  }

  handleFormSubmit = (res) => {
    this.getQuestions(res)
  }

  handleOk = (detail) => {
    this.props.handleOk(detail)
  }

  onClose = () => {
    this.props.onClose()
  }

  get renderForm () {
    const {subject, questionsType, examType} = this.props
    return (
      <BaseForm
        rowItems={[
          {
            label: '课程类型',
            field: 'subject_id',
            formItemLayout: {
              labelCol: { span: 2 },
              wrapperCol: { span: 21 }
            },
            content: (
              <TagSelect isRadio>
                {subject.map((item, index) => <TagSelect.Option value={item.subject_id} key={index}>{item.subject_text}</TagSelect.Option>)}
              </TagSelect>
            ),
          }
        ]}
        items={[
          {
            label: '考试类型',
            field: 'exam_id',
            formItemLayout: {
              labelCol: { span: 10 },
              wrapperCol: { span: 14 }
            },
            content: (
              <Select>
                {examType.map((item, index) => <Option value={item.exam_id} key={index}>{item.exam_name}</Option>)}
              </Select>
            ),
          },
          {
            label: '题目类型',
            field: 'questions_type_id',
            formItemLayout: {
              labelCol: { span: 10 },
              wrapperCol: { span: 14 }
            },
            content: (
              <Select>
                {questionsType.map((item, index) => <Option value={item.questions_type_id} key={index}>{item.questions_type_text}</Option>)}
              </Select>
            ),
          },
          {
            isFormItem: false,
            content: (
                <Button
                    type="primary"
                    icon="search"
                    htmlType="submit"
                    style={{
                        margin: '3px 0 0 50px',
                    }}
                >查询
                </Button>
            ),
          }
        ]}
        onSubmit={this.handleFormSubmit}
      />
    )
  }

  render () {
    const {questions} = this.props;
    return (
      <Drawer
        title="所有题目"
        placement='right'
        closable={false}
        width={840}
        onClose={this.onClose}
        visible={this.state.visible}
      >
        <div >
          {this.renderForm}
        </div>
        <div >
        <QuestionItems  {...this.props} items={questions} handleOk={this.handleOk}/>
        {/* <QuestionDetailModul visible="" detal={this.state.questions_detail} /> */}
        </div>
      </Drawer>
    )
  }
}

class QuestionItem extends Component {
  constructor (props) {
    super(props)
    this.state = {}
  }

  remove = () => {
    this.props.onRemoveCallback()
  }

  render () {
    const {title, questions_stem, index, } = this.props;

    return (
      <div className={styles.questionitem}>
        <h4>{index}：{title} <a href="javascript:;" style={{float: 'right'}} onClick={this.remove}>删除</a></h4>
        <ReactMarkdown className="react-markdown" source={questions_stem}></ReactMarkdown>
      </div>
    )
  }
}

let mapState = store=>{
  return {...store.addExam, ...store.main}
}
let mapDispatch = dispatch => {
  return {
    initData(payload){
      dispatch({type:'addExam/requestData', ...payload});
    },
    updateExam (payload) {
      return dispatch({type:'addExam/updateExam', ...payload});
    }
  }
}

@connect(mapState,mapDispatch)
class AddExam extends Component {

  constructor (props) {
    super(props)
    this.state = {
      questions: this.props.examDetail.questions,
      visible: false
    }
  }

  componentDidMount () {
    this.props.initData()
  }

  onRemoveCallback = (index) => {
    const {questions} = this.state
    Modal.confirm({
      title: '确认提示',
      content: '是否要删除该题目？',
      okText: '确认',
      cancelText: '取消',
      onOk: () => {
        questions.splice(index, 1)
        this.setState({
          questions: questions
        })
      }
    });
  }

  createExam = () => {
    this.props.updateExam({
      id: this.props.examDetail.exam_exam_id,
      question_ids: JSON.stringify(this.state.questions.map((item) => {
        return item.questions_id
      }))
    }).then(() => {
      this.props.history.push({
        pathname: basePath + '/main/exam/list',
        state:{
          parent:'考试管理',
          child:'试卷列表',
          id:'main-examList'
        }
      })
    })

  }

  onClose = () => {
    this.setState({
      visible: false,
    });
  }

  

  handleOk = (detail) => {
    this.setState({
      questions: this.state.questions.concat([detail])
    })
  }

  render() {
    const {examDetail} = this.props
    const {questions} = this.state

    return (
      <BaseContent>
        <Button icon="plus" onClick={() => this.setState({visible: true})}>添加新题</Button>
        <div className={styles.exam}>
          <h2>{examDetail.title}</h2>
          <p>考试时间：1小时30分钟		监考人：刘于       开始考试时间：2018.9.10  10:00  阅卷人：刘于</p>
          <span className={styles.juan}></span>
          <div>
            {
              questions.map((item, index) => <QuestionItem {...item} index={index+1} key={index} onRemoveCallback={() => this.onRemoveCallback(index)} />)
            }
          </div>
          <Button type="primary" onClick={this.createExam}>
            创建试卷
          </Button>
        </div>

        <QuestionDrawer visible={this.state.visible} onClose={this.onClose} handleOk={this.handleOk}/>
      </BaseContent>
    )
  }
}

export default AddExam
